@use './abstracts' as *;

.movie-list-container {
    display: flex;
    flex-flow: column;
    width: 50vw;
    margin: 0 auto;
    text-align: center;


}

.movie-list {
    @include media(">=tablet") {

    }
    .movie-item {
        .movie-title {
            padding: 1rem;
            .movie-link {

            }
        }
        .movie-description {
            padding: 1rem;
        }
        .movie-trailer {
            position: relative;
            width: 100%;
            height: 0;
            padding-top: 56.25%; /* 16:9 Aspect Ratio (divide 9 by 16 = 0.5625) */

            iframe {
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
            }
        }
        .movie-program {
            padding: 1rem;
            .program-heading {
                padding: 1rem;
            }

            .program-details {
                .program-list {
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    gap: 2rem;

                    .program-cinema {}
                    .program-date {}
                    .program-ticket {}
                }
            }
        }
    }
}